CE,YAAY;IACZ,cAAc;IACd,eAAe;IACf,kBAAkB;IAClB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,YAAY;EACd;;EAEA;IACE,aAAa;IACb,8BAA8B;EAChC;;EAEA;IACE,iBAAiB;IACjB,YAAY;EACd;;EAEA;;;;;;;;;KASG;;EAEH;IACE,WAAW;IACX,YAAY;EACd;;EAEA;IACE,eAAe;EACjB;AACF","sourcesContent":["html {\n  overflow: -moz-scrollbars-vertical;\n  overflow-y: scroll;\n  scrollbar-width: none;\n  -ms-overflow-style: none;\n  overflow-x: hidden;\n}\n\nhtml::-webkit-scrollbar {\n  display: none;\n  width: 0;\n  height: 0;\n}\n\nbody {\n  font-family: \"DM Sans\", \"Segoe UI\", Tahoma, Geneva, Verdana, sans-serif;\n  min-height: 100vh;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0 20px;\n\n  color: var(--text-primary);\n  background-image: linear-gradient(\n    225deg,\n    #5d9fff 0%,\n    #b8dcff 48%,\n    #6bbbff 100%\n  );\n}\n\n.weather-app-container {\n  width: 100%;\n  max-width: 1200px;\n  margin: auto;\n  padding: 10px 0 15px 0;\n}\n\n/* --- Header --- */\n.app-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 15px;\n}\n\n.app-logo {\n  font-size: 1.5rem;\n  font-weight: bold;\n  display: inline-flex;\n  align-items: center;\n  gap: 0.5rem;\n}\n\n#weather-search-form {\n  display: flex;\n  align-items: center;\n\n  background: rgba(255, 255, 255, 0.2);\n  border-radius: 50px;\n  overflow: hidden;\n}\n\n#city-input,\n#city-input:focus {\n  padding: 8px 20px;\n  font-size: 1rem;\n  color: var(--text-primary);\n  background: transparent;\n  border: none;\n  outline: none;\n}\n\ninput:-webkit-autofill {\n  transition: background-color 100000s ease-in-out 0s;\n  -webkit-text-fill-color: var(--text-primary) !important;\n}\n\n#city-input::placeholder {\n  color: var(--text-secondary);\n}\n\n.search-button,\n.unit-toggle {\n  background: rgba(255, 255, 255, 0.3);\n  border: none;\n  color: var(--text-primary);\n  border-radius: 50%;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.search-button {\n  margin-right: 6px;\n  width: 30px;\n  height: 30px;\n}\n\n.unit-toggle {\n  width: 50px;\n  padding: 5px 15px;\n  font-size: 1rem;\n  border-radius: 20px;\n}\n\n.unit-toggle,\n.search-button,\n#weather-search-form {\n  background: rgba(255, 255, 255, 0.15);\n  background-color: rgba(255, 255, 255, 0.3);\n  border-radius: var(--border-radius);\n  border: 1px solid rgba(255, 255, 255, 0.35);\n  box-shadow:\n    0 4px 20px rgba(0, 0, 0, 0.1),\n    inset 0 1px 3px rgba(255, 235, 120, 0.6);\n}\n\n/* --- Main Layout --- */\nmain {\n  display: grid;\n  grid-template-columns: 0.85fr 1fr;\n  gap: 15px;\n  position: relative;\n}\n\n.main-content.hidden,\n.right-panel.hidden {\n  opacity: 0;\n  transition: opacity 0.3s ease-out;\n}\n\n.main-content.visible,\n.right-panel.visible {\n  opacity: 1;\n  transition: opacity 0.3s ease-in;\n}\n\n.card {\n  padding: clamp(10px, 3vw, 20px) clamp(15px, 3vw, 20px);\n  background: rgba(255, 255, 255, 0.15);\n  background-color: rgba(255, 255, 255, 0.3);\n  border-radius: var(--border-radius);\n  border: 1px solid rgba(255, 255, 255, 0.35);\n  box-shadow:\n    0 4px 20px rgba(0, 0, 0, 0.1),\n    inset 0 1px 3px rgba(255, 235, 120, 0.6);\n}\n\n/* --- Current Weather --- */\n.current-weather {\n  padding-left: clamp(15px, 5vw, 30px);\n  padding-right: clamp(15px, 5vw, 30px);\n  margin-bottom: 10px;\n\n  background-image: linear-gradient(\n    135deg,\n    rgba(255, 235, 120, 0.1),\n    rgba(120, 255, 214, 0.1)\n  );\n}\n.current-weather-container {\n  display: grid;\n  grid-template-columns: 0.9fr 1fr;\n}\n.location {\n  font-size: clamp(1.8rem, 4vw, 2.5rem);\n  font-weight: 500;\n  width: 100%;\n}\n.parent-location {\n  font-size: clamp(1rem, 2vw, 1.3rem);\n  font-weight: 500;\n  width: 100%;\n}\n.current-temp {\n  font-size: clamp(4rem, 10vw, 6rem);\n  font-weight: 700;\n  margin: 0.5rem 0 0 0;\n  line-height: 1;\n  display: inline-block;\n}\n.current-condition {\n  font-size: clamp(1rem, 2.5vw, 1.2rem);\n}\n#current-weather-icon {\n  display: flex;\n  align-items: end;\n  justify-content: center;\n}\n#current-weather-icon img {\n  filter: drop-shadow(0px 20px 10px rgba(0, 0, 0, 0.2));\n}\n#current-weather-description {\n  font-size: clamp(1rem, 2.5vw, 1.2rem);\n  font-style: italic;\n  color: var(--text-secondary);\n}\n\n/* --- Weather Details --- */\n.weather-details {\n  white-space: no-wrap;\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));\n  gap: 10px;\n}\n.detail-card {\n  padding: clamp(10px, 2vw, 15px);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  aspect-ratio: 1 / 1;\n  width: 100%;\n  max-width: 180px;\n  margin: 0 auto;\n}\n.detail-title {\n  font-size: clamp(0.8rem, 1.5vw, 1rem);\n  font-weight: bold;\n  color: var(--text-secondary);\n}\n.detail-value {\n  font-size: clamp(1rem, 2vw, 1.3rem);\n  font-weight: 500;\n}\n.detail-card img {\n  width: 80px;\n  max-width: 80%;\n  height: auto;\n}\n\n/* --- Right Panel --- */\n.right-panel {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  height: 100%;\n  max-width: 640.545px;\n  /* overflow-y: hidden; */\n}\n\n.hourly-forecast h3,\n.seven-day-forecast h3 {\n  margin-bottom: 1rem;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.2);\n  padding-bottom: 5px;\n}\n\n.seven-day-forecast {\n  flex: 1;\n}\n\n.hourly-forecast-container {\n  display: flex;\n  gap: 10px;\n  overflow-x: auto;\n  scroll-behavior: smooth;\n  padding-bottom: 10px;\n  max-height: 180px;\n}\n.hourly-forecast-container::-webkit-scrollbar {\n  height: 5px;\n}\n.hourly-forecast-container::-webkit-scrollbar-track {\n  background: transparent;\n}\n.hourly-forecast-container::-webkit-scrollbar-thumb {\n  background: rgba(255, 255, 255, 0.3);\n  border-radius: 10px;\n  backdrop-filter: blur(5px);\n}\n\n.daily-forecast-container {\n  padding-right: 5px;\n  max-height: 340px;\n  overflow-y: auto;\n  scroll-behavior: smooth;\n}\n.daily-forecast-container::-webkit-scrollbar {\n  width: 5px;\n}\n.daily-forecast-container::-webkit-scrollbar-track {\n  background: transparent;\n}\n.daily-forecast-container::-webkit-scrollbar-thumb {\n  background: rgba(255, 255, 255, 0.3);\n  border-radius: 10px;\n  backdrop-filter: blur(5px);\n}\n\n.hourly-item {\n  font-weight: 500;\n  display: flex;\n  flex-shrink: 0;\n  flex-direction: column;\n  align-items: center;\n  min-width: 80px;\n  text-align: center;\n  overflow: hidden;\n}\n.daily-item {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 0px 0;\n}\n\n.daily-item:not(:last-child) {\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\n}\n\n.daily-item-day {\n  flex-basis: 20%;\n}\n.daily-item-condition {\n  flex-basis: 40%;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n.daily-item-temp {\n  flex-basis: 20%;\n  text-align: right;\n}\n.hourly-item img {\n  width: 130px;\n  height: 100px;\n  object-fit: cover;\n  filter: drop-shadow(0px 10px 5px rgba(0, 0, 0, 0.15));\n}\n.daily-item-condition img {\n  width: 60px;\n  object-fit: cover;\n  filter: drop-shadow(0px 10px 5px rgba(0, 0, 0, 0.1));\n}\n\n.current-temp-unit {\n  font-size: clamp(3rem, 9vw, 5rem);\n}\n\n/* Spinner Styles */\n.spinner-container {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.7);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 9999;\n}\n\n.spinner {\n  border: 8px solid rgba(255, 255, 255, 0.3);\n  border-top: 8px solid var(--text-primary);\n  border-radius: 50%;\n  width: 60px;\n  height: 60px;\n  animation: spin 2s linear infinite;\n}\n\n.spinner-small {\n  border: 4px solid rgba(255, 255, 255, 0.3);\n  border-top: 4px solid var(--text-primary);\n  border-radius: 50%;\n  width: 20px;\n  height: 20px;\n  animation: spin 2s linear infinite;\n  display: none;\n  margin-left: 10px;\n}\n\n@keyframes spin {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.weather-app-container {\n  opacity: 0;\n  transition: opacity 0.5s ease-in-out;\n}\n\n.weather-app-container.loaded {\n  opacity: 1;\n}\n\n.mobile-search-button {\n  display: none;\n}\n\n@media (max-width: 1000px) {\n  body {\n    padding: 12px;\n  }\n\n  main {\n    display: flex;\n    flex-direction: column;\n    gap: 20px;\n  }\n\n  form {\n    display: none !important;\n  }\n\n  .current-weather {\n    margin-bottom: 20px;\n  }\n\n  .current-weather-container {\n    grid-template-columns: 1fr 1fr;\n  }\n\n  .current-weather-info {\n    max-width: 120px;\n    position: relative;\n  }\n\n  p#location {\n    position: absolute;\n    top: 20;\n    left: 0;\n    white-space: nowrap;\n    z-index: 10;\n  }\n\n  p#parent-location {\n    margin-top: clamp(30px, 9.5vw, 50px);\n  }\n  #current-weather-icon img {\n    width: 190px;\n    height: 172px;\n    object-fit: cover;\n  }\n\n  #current-weather-description {\n    font-size: 0.8rem;\n  }\n\n  .weather-details {\n    grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));\n    gap: 10px;\n  }\n\n  .detail-card {\n    white-space: no-wrap;\n    padding: clamp(10px, 2vw, 15px) clamp(8px, 2vw, 15px);\n  }\n\n  .detail-card img {\n    width: clamp(50px, 9vw, 70px);\n  }\n\n  .right-panel {\n    gap: 20px;\n    max-width: unset;\n  }\n\n  .daily-forecast-container {\n    overflow-y: visible;\n    max-height: unset;\n  }\n\n  .daily-item {\n    font-size: 0.8rem;\n    padding: 0 0;\n  }\n\n  .weather-app-container {\n    padding: 0;\n    max-height: unset;\n    overflow-y: visible;\n  }\n\n  .hourly-forecast-container {\n    height: auto;\n    overflow-x: auto;\n    gap: 0;\n  }\n\n  .mobile-search-button {\n    padding: 5px;\n    display: unset;\n    font-size: 1rem;\n    border-radius: 50%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    width: 40px;\n    height: 40px;\n  }\n\n  .utils {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n  }\n\n  .unit-toggle {\n    font-weight: bold;\n    height: 40px;\n  }\n\n  /* .current-weather-container {\n    display: flex;\n    justify-content: space-between;\n  }\n\n  #current-weather-icon {\n    width: 80%;\n    max-width: 200px;\n    flex-basis: 60%;\n  } */\n\n  .hourly-item img {\n    width: 80px;\n    height: 60px;\n  }\n\n  .daily-item-condition {\n    flex-basis: 55%;\n  }\n}\n"],"sourceRoot":""}]);
